export const initializeSidebarAndMenuItem = () =>
  miro.initialize({
    extensionPoints: {
      bottomBar: {
        title: "widget counter",
        svgIcon: icon24,
        onClick: () => {
          miro.board.ui.openLeftSidebar("app.html");
        },
      },
      getWidgetMenuItems: (widgets) => {
        const infoToDisplay = infoForMenuItem(widgets);
        if (!infoToDisplay) {
          return Promise.resolve([]);
        }
        return Promise.resolve([
          {
            tooltip: "Space Measure",
            svgIcon: icon24,
            onClick: () => {
              const area =
                infoToDisplay.area !== 0
                  ? `Area :${infoToDisplay.area} | `
                  : "";
              const perimeter =
                infoToDisplay.perimeter !== 0
                  ? `Perimeter :${infoToDisplay.perimeter} | `
                  : "";
              const length =
                infoToDisplay.length !== 0
                  ? `Length :${infoToDisplay.length}`
                  : "";

              miro.showErrorNotification(`${area}${perimeter}${length}`);
            },
          },
        ]);
      },
    },
  });
